import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http"
import {  } from "@angular/core";

import { Response, Product } from './product/product'

import { Observable } from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class ProductService {

  constructor(private http: HttpClient) { }

  getProducts():Observable<Response>{
    return this.http.get<Response>('/api/product')
  }
  addPorduct(pro: Product): Observable<Product> {
    return this.http.post<Product>('/api/product', pro)
  }
  deleteProduct(id: string): Observable<{}> {
    const url = `/api/product/${id}`
    return this.http.delete(url)
  }
  updateProduct(pro:Product): Observable<Product> {
    const url = `/api/product/${pro._id}`
    return this.http.put<Product>(url, pro)
  }
}
