import { Component, OnInit } from '@angular/core';

import { Product } from 'src/app/product/product'
import { ProductService } from '../product.service';
import { HttpClient } from '@angular/common/http';



@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css'],
  providers: [ProductService]
})
export class ProductComponent implements OnInit {

  products: Product[]
  editProduct: Product
 

  constructor(private productService:ProductService, private http: HttpClient, ) { }

  ngOnInit(): void {
    this.getProducts()
    
  }
  

  getProducts():void{
    this.productService.getProducts().subscribe(response => (this.products = response.data))
  }

  addProduct(name: string, description: string, precio: number): void {
    this.editProduct = undefined
    // comformar el cuerpo del producto
    if(!name || !description || !precio){
      return
    }
    // y comprobar que el objeto no este vacio
    // event.preventDefault()
    const newProduct:Product = { name, description, precio } as Product
    console.log(newProduct);
    
    this.productService.addPorduct(newProduct).subscribe(() => this.getProducts())

  }
  delete(pro: Product): void {
    this.products = this.products.filter(h => h !== pro)
    this.productService.deleteProduct(pro._id).subscribe(()=> console.log("producto borrado"))
  }

  edit(pro) {
    this.editProduct = pro
  }

  update() {
    if(this.editProduct){
      this.productService.updateProduct(this.editProduct).subscribe(()=>{
        this.getProducts()
      })
      this.editProduct = undefined
    }
  }
}
